.headerMain {
    display: flex;
    max-width: 100%;
    max-height: 500px;
    justify-content: center;
    background-color: #ECF9FF;
}

.headerMain2 {
    display: flex;
    max-width: 80%;
    align-items: center;
}

.headerColOne>h1 {
    color: var(--Primary, #060640);
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: 50px;
    font-style: normal;
    font-weight: 700;
    line-height: 65px;
    width: 423px;
    /* 130% */
}

.headerColOne>p {
    margin-top: 20px;
    width: 402px;
}

.headerColOne>button {
    margin-top: 40px;
    display: inline-flex;
    color: #FFF;
    text-align: center;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 15px;
    /* 107.143% */
    height: 40px;
    padding: 0px 15px 0px 15px;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
    border-radius: 6px;
    background: var(--Primary, #060640);
}

.headerColTwo>img {
    max-width: 600px;
}