.ctaOneMainDiv {
    /* max-width: 1000px; */
    margin-bottom: 200px;
}

.ctaOneHeading {
    display: flex;
    flex-direction: column;
    width: 100%;
    justify-content: center;
    align-items: center;
    margin-top: 80px;
}

.ctaOneHeading>h2 {
    color: var(--Primary, #060640);
    text-align: center;
    font-size: 40px;
    font-style: normal;
    font-weight: 700;
    line-height: 55px;
}

.ctaOneHeading>p {
    /* 175% */
    width: 431px;
    text-align: center;
}

.ctaOneMainDiv2 {
    display: flex;
    margin-top: 50px;
    justify-content: space-evenly;
}

.ctaOneMainDiv2Inner {
    display: flex;
    justify-content: space-between;
    width: 1200px;
}

.ctaOneCol {
    /* background-color: aqua; */
    width: 370px;
    height: 300px;
    border-radius: 14px;
    border: 2px solid rgba(6, 6, 64, 0.07);
    /* opacity: 0.04; */
}

.ctaOneCol>img {
    margin: 30px 0px 0px 30px;
}

.ctaOneCol>h3 {
    color: var(--Primary, #060640);
    margin: 45px 0px 0px 30px;
    font-size: 22px;
    font-style: normal;
    font-weight: 500;
    line-height: 34px;
    /* 141.667% */
}

.ctaOneCol>p {
    margin: 5px 10px 10px 30px;
}

.ctaOneColTwo {
    border-radius: 14px;
    /* opacity: 0.04; */
    background: #FFF;
    box-shadow: 0px 30px 50px 0px #03032514;
}